import React from "react";
import gallery from "../../../assets/images/preview1.png";
import addIcon from "../../../assets/images/add.png";
import axios from "axios";

class Media extends React.Component {
  state = {
    selectedFile: null
  };

  selectFileHandler = e => {
    this.setState({
      selectedFile: e.target.files[0]
    });
  };

  uploadFileHandler = () => {
    const fd = new FormData();
    fd.append("image", this.state.selectedFile, this.state.selectedFile.name);
    axios.post("https://2.evollai.com/api/topic", fd);
  };

  render() {
    return (
      <div className="media">
        <h2 className="title">Media</h2>
        <p className="paragraph">
          Choose images for the overview section of your topic.
        </p>
        <div className="gallery-list">
          <div className="gallery upload">
            <div>
              <img src={addIcon} alt="add" onClick={this.uploadFileHandler} />
              <input type="file" onChange={this.selectFileeHandler} />
            </div>
          </div>
          <div className="gallery">
            <img src={gallery} alt="gallery" />
          </div>
          <div className="gallery">
            <img src={gallery} alt="gallery" />
          </div>
          <div className="gallery">
            <img src={gallery} alt="gallery" />
          </div>
        </div>
        <button className="G-button" onClick={this.props.moveNextPage}>
          Continue
        </button>
      </div>
    );
  }
}

export default Media;
